
.scrollingThing {
    overflow-y: hidden;
}
.scrollingThing:hover {
    overflow-y: auto;
}
button {
    background-color: transparent;
    border-width: 0px;
}
input {
    margin-top: 20px;
    box-sizing: border-box;
    border: 2px solid rgb(235,235,235);
    outline: none; 
    padding: 12px 20px;
    height: 50px;
    border-radius: 10px;
    padding-bottom: 20px;
    width: 200px
}
.navigation {
    position: fixed;
}
.todo-div {
    display: inline-block;
    width: 200px;
    
    margin-left: 100px;
    /*float: left; */
}
.login-button {
    border: 1cm;
    background-color: #8b4389;
    border-radius: 5px;
}

.video-outer-div {
    position: absolute;
    bottom: -95vh;
    z-index: -3;
    
    display: inline-block; 
    
    
    margin-left: calc(60px + 20vw);
    overflow-y: auto;
    height: auto;
}
input:focus { 
    border: 1px solid #0af; 
    outline: none; 
    -moz-box-shadow: 0px 0px 15px rgba(0, 170, 255,.5);
    -webkit-box-shadow: 0px 0px 15px rgba(0, 170, 255,.5);
    box-shadow: 0px 0px 15px rgba(0, 170, 255,.5); }

.todo-outer-div {
    margin-left: 300px;
    margin-top: 100px;
    display: inline-block; 
    overflow-X: auto; 
    position: relative; 
    height: calc(100vh - 105px);
    width: calc(100vw - 300px);

}

body {
    overflow: scroll;
}